html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5,
h6, p, blockquote, pre, a, abbr, acronym, address, big,
cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small,
strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt,
dd, ol, ul, li, fieldset, form, label, legend, table, caption,
tbody, tfoot, thead, tr, th, td, article, aside, canvas, details,
embed, figure, figcaption, footer, header, hgroup, menu, nav,
output, ruby, section, summary, time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  vertical-align: baseline;
}

html {
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

img, fieldset, a img {
  border: none;
}

input[type="text"],
input[type="email"],
input[type="tel"],
textarea {
  -webkit-appearance: none;
}

input[type="submit"],
button {
  cursor: pointer;
}

input[type="submit"]::-moz-focus-inner,
button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

textarea {
  overflow: auto;
}

input, button {
  margin: 0;
  padding: 0;
  border: 0;
}

div, input, textarea, select, button,
h1, h2, h3, h4, h5, h6, a, span, a:focus {
  outline: none;
}

ul, ol {
  list-style-type: none;
}

table {
  border-spacing: 0;
  border-collapse: collapse;
  width: 100%;
}

html {
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

@media (prefers-reduced-motion: reduce) {
  * {
    transition: none !important;
  }
}

.icon-facebook {
  width: 0.53em;
  height: 1em;
  fill: #000;
}

body {
  background: #FFF;
  font-size: 50px;
  font-size: calc(16px + (50 - 16) * ((100vw - 600px) / (1000 - 600)));
  line-height: 1.4;
}

@media (max-width: 600px) {
  body {
    font-size: 16px;
  }
}

@media (min-width: 1000px) {
  body {
    font-size: 50px;
  }
}

.header {
  max-width: 845px;
  width: 100%;
  margin: 50px auto;
}

.header:after {
  content: " ";
  display: table;
  clear: both;
}

.header__container {
  width: 100%;
  text-align: center;
  position: relative;
}

.logo {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
}

.logo__link {
  text-decoration: none;
  font-size: 26px;
  font-family: "Martel", sans-serif;
  line-height: 0.669;
}

.logo__link:hover {
  font-weight: bold;
}

.logo__part1, .logo__part2 {
  float: left;
}

.logo__part1 {
  color: #0e1d2b;
}

.logo__part2 {
  color: #ffa326;
}

.nav {
  float: right;
  right: 0;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
}

.nav a {
  font-size: 16px;
  font-family: "Martel", sans-serif;
  color: #0e1d2b;
  line-height: 1.087;
  text-decoration: none;
  margin-left: 20px;
  box-sizing: border-box;
  float: left;
  border-bottom: 3px solid #ffffff;
}

.nav a:hover {
  font-weight: bold;
  border-bottom: 3px solid #ffa326;
}

.footer {
  background-color: #050b10;
}

.footer:after {
  content: " ";
  display: table;
  clear: both;
}

.copywrite, .website {
  font-size: 12px;
  font-family: "Martel";
  color: #ffa326;
  line-height: 3.821;
  float: left;
}

.website {
  float: right;
  text-decoration: none;
}

.hero:after {
  content: " ";
  display: table;
  clear: both;
}

.hero__part1, .hero__part2 {
  background-size: cover;
  background-repeat: no-repeat;
  background-position: 100% 100%;
  width: 50%;
  float: left;
}

.part1__bckg, .part2__bckg {
  height: 395px;
  opacity: 0.92;
  position: relative;
}

.part1__bckg {
  background-color: #0e1d2b;
}

.part2__bckg {
  background-color: #ff9f1c;
}

.part1__wrap, .part2__wrap {
  position: absolute;
  top: 50%;
  right: 0;
  transform: translateY(-50%);
  max-width: 422px;
  width: 100%;
}

.part2__wrap {
  left: 0;
}

.part1__title, .part2__title {
  font-size: 65px;
  font-family: "Rubik";
  line-height: 0.923;
}

.part1__title {
  color: #ffa326;
}

.part2__title {
  margin-left: 17.5%;
  color: #ffffff;
}

.part1__text, .part2__text {
  font-size: 14px;
  font-family: "Martel";
  line-height: 1.429;
}

.part1__text {
  display: inline-block;
  color: #ffffff;
}

.part2__text {
  display: inline-block;
  margin-left: 17.5%;
  color: #0d1e2d;
}

.showme {
  display: inline-block;
  margin-left: 10px;
  width: 159px;
  height: 38px;
  box-shadow: 6px 4px 8px 1px rgba(0, 0, 0, 0.75);
}

.burgers {
  box-sizing: border-box;
  padding: 60px 0;
}

.burgers:after {
  content: " ";
  display: table;
  clear: both;
}

.price {
  width: 22.344827586%;
  float: left;
  display: inline-block;
  border: 1px;
  box-sizing: border-box;
  text-align: center;
}

.price:after {
  content: " ";
  display: table;
  clear: both;
}

.price__center {
  margin: 0 14%;
}

.price__img {
  width: 100%;
}

.price__title {
  font-size: 22.63px;
  font-family: "Rubik";
  color: #f39c22;
  line-height: 0.692;
}

.price__text {
  font-size: 12.18px;
  font-family: "Martel";
  color: #0b1d2c;
  line-height: 1.286;
}

.price__kj {
  display: inline-block;
}

.price__bucks {
  display: inline-block;
}

.price__button {
  width: 100%;
  height: 30px;
}

.waitsect {
  background-position: 100% 100%;
  background-repeat: no-repeat;
  background-size: cover;
}

.waitsect__bckg {
  background: linear-gradient(#0e1d2b, #ffa326);
  opacity: 0.92;
}

.waitsect__wrap {
  height: 404px;
  position: relative;
}

.waitsect__title {
  display: inline-block;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  font-size: 60px;
  font-family: "Rubik";
  color: #ffffff;
  line-height: 1;
}

.waitsect__table {
  display: inline-block;
  position: absolute;
  top: 50%;
  right: 0;
  transform: translateY(-50%);
  font-size: 16px;
  font-family: "Martel";
  color: #ffffff;
  line-height: 0.979;
  text-align: left;
  border: 3px solid #ffa326;
}

.waitsect__table table {
  max-width: 100%;
  padding: 30px 0;
}

.waitsect__table table tr {
  padding: 15px;
}

.waitsect__table table tr td {
  padding: 15px;
}

.photos:after {
  content: " ";
  display: table;
  clear: both;
}

.photos__wrap {
  text-align: center;
}

.photos img {
  width: 100%;
}

.top__photo {
  box-sizing: border-box;
  width: 33.333333333333333333333333333333%;
  float: left;
  margin: 0;
}

.top__photo img {
  height: 184px;
  max-width: 100%;
  transform: scale(1.01);
}

.bottom__photo {
  box-sizing: border-box;
  width: 16.666666666666666666666666666667%;
  float: left;
  margin: 0;
  margin-top: -10px;
  z-index: 900;
}

.bottom__photo img {
  height: 92px;
  max-width: 100%;
  transform: scale(1.01);
}

.gallery__top {
  position: relative;
  margin: 0;
}

.gallery__top:after {
  content: " ";
  display: table;
  clear: both;
}

.gallery__bottom {
  position: relative;
  margin: 0;
}

.gallery__bottom:after {
  content: " ";
  display: table;
  clear: both;
}

.board {
  position: absolute;
  top: -20px;
  left: 0;
  width: 100.2%;
  height: 10px;
  border: 3px solid #ff9f1c;
  box-sizing: border-box;
  z-index: 999;
}

.contact {
  text-align: center;
}

.contact__container {
  background-color: #0d1e2d;
}

.contact__wrap {
  margin: 0 auto;
  padding: 50px 0;
}

.contact__wrap:after {
  content: " ";
  display: table;
  clear: both;
}

.contact__touchus, .contact__location {
  box-sizing: border-box;
  text-align: left;
}

.contact__touchus {
  float: left;
  width: 35.310%;
  margin: 0;
}

.contact__title {
  font-size: 12.18px;
  font-family: "Martel";
  color: #ffffff;
  line-height: 1.286;
  text-align: left;
  margin-bottom: 7px;
}

.contact__location {
  float: right;
  width: 43.7241%;
}

.contact__address {
  margin-top: 25px;
  font-size: 18.52px;
  font-family: "Martel";
  color: #ffffff;
  line-height: 0.979;
  text-align: center;
}

.contact__call {
  margin-top: 25px;
  margin-left: 20px;
  font-size: 18.52px;
  font-family: "Martel";
  color: #ffffff;
  line-height: 0.979;
  text-align: center;
  float: left;
}

.contact__phone {
  margin-top: 10px;
  margin-left: 10px;
  font-size: 46.83px;
  font-family: "Martel";
  color: #ffa326;
  font-weight: bold;
  line-height: 0.979;
  text-align: center;
  text-decoration: none;
  float: left;
}

.touchus__form {
  width: 100%;
  display: block;
}

.touchus__form input, .touchus__form textarea {
  display: block;
  width: 100%;
  height: 30px;
  color: #0b1d2c;
  font-size: 12.18px;
  line-height: 1;
  font-family: "Martel";
  margin-bottom: 7px;
  text-align: left;
  padding-left: 7%;
  padding-top: 5px;
}

.touchus__form input::placeholder, .touchus__form textarea::placeholder {
  color: #0b1d2c;
}

.touchus__form textarea {
  height: 90px;
}

.touchus__form input[type="submit"] {
  font-size: 16px;
  color: #ffffff;
  font-weight: bold;
  line-height: 0.979;
  text-align: center;
  background-color: #ffa326;
  padding: 0;
}

#map {
  height: 175px;
  height-width: 100%;
}

.out {
  display: block;
}

.container {
  max-width: 725px;
  width: 100%;
  margin: 0 auto;
}

.wrap__title {
  margin-top: 60px;
  margin-bottom: 29px;
  font-size: 60px;
  font-family: "Rubik";
  color: #0d1624;
  line-height: 1;
}

.wrap__text {
  margin-bottom: 60px;
  font-size: 12.18px;
  font-family: "Martel";
  color: #0b1d2c;
  line-height: 1.286;
}
